// app/(dashboard)/dashboard/mistakes/page.tsx
"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { supabase } from "@/lib/supabaseClient"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  BookX,
  Play,
  CheckCircle,
  HelpCircle,
  ListChecks,
  PenLine,
  MessageSquare,
  TrendingUp,
  Award,
  Target,
} from "lucide-react"
import type { Subject, Question, QuestionType } from "@/types/supabase"

// È°åÂûãÂúñÁ§∫Â∞çÊáâ
const typeIcons: Record<string, React.ElementType> = {
  true_false: CheckCircle,
  single_choice: HelpCircle,
  multiple_choice: ListChecks,
  fill_in_blank: PenLine,
  essay: MessageSquare,
}

// È°åÂûãÈ°èËâ≤Â∞çÊáâ
const typeColors: Record<string, string> = {
  true_false: "text-green-600 bg-green-100",
  single_choice: "text-blue-600 bg-blue-100",
  multiple_choice: "text-purple-600 bg-purple-100",
  fill_in_blank: "text-amber-600 bg-amber-100",
  essay: "text-pink-600 bg-pink-100",
}

export default function MistakesPage() {
  const router = useRouter()
  const [subjects, setSubjects] = useState<Subject[]>([])
  const [questionTypes, setQuestionTypes] = useState<QuestionType[]>([])
  const [questions, setQuestions] = useState<Question[]>([])
  const [loading, setLoading] = useState(true)

  // ÁØ©ÈÅ∏Ê¢ù‰ª∂
  const [filterSubject, setFilterSubject] = useState<string>("all")
  const [filterType, setFilterType] = useState<string>("all")
  const [sortBy, setSortBy] = useState<string>("wrong_count")

  // Áµ±Ë®àË≥áÊñô
  const [stats, setStats] = useState({
    total: 0,
    mastered: 0,
    inProgress: 0,
  })

  // ËºâÂÖ•Ë≥áÊñô
  const fetchData = async () => {
    const { data: { user } } = await supabase.auth.getUser()
    if (!user) return

    // ÂèñÂæóÁßëÁõÆ
    const { data: subjectsData } = await supabase
      .from("subjects")
      .select("*")
      .eq("user_id", user.id)
      .order("title")

    if (subjectsData) setSubjects(subjectsData)

    // ÂèñÂæóÈ°åÂûã
    const { data: typesData } = await supabase
      .from("question_types")
      .select("*")

    if (typesData) setQuestionTypes(typesData)

    // ÂèñÂæóÁµ±Ë®à
    await fetchStats(user.id)

    // ÂèñÂæóÈåØÈ°å
    await fetchQuestions(user.id)

    setLoading(false)
  }

  const fetchStats = async (userId: string) => {
    // Á∏ΩÂÖ±ÂÅöÈÅéÁöÑÈ°åÁõÆ
    const { count: totalCount } = await supabase
      .from("questions")
      .select("*", { count: "exact", head: true })
      .eq("user_id", userId)
      .gt("attempt_count", 0)

    // Â∑≤ÁÜüÁ∑¥ÔºàÈÄ£Á∫åÁ≠îÂ∞ç >= 3Ôºâ
    const { count: masteredCount } = await supabase
      .from("questions")
      .select("*", { count: "exact", head: true })
      .eq("user_id", userId)
      .gte("consecutive_correct", 3)

    // Á∑¥Áøí‰∏≠
    const { count: inProgressCount } = await supabase
      .from("questions")
      .select("*", { count: "exact", head: true })
      .eq("user_id", userId)
      .gt("attempt_count", 0)
      .lt("consecutive_correct", 3)

    setStats({
      total: totalCount || 0,
      mastered: masteredCount || 0,
      inProgress: inProgressCount || 0,
    })
  }

  const fetchQuestions = async (userId?: string) => {
    if (!userId) {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) return
      userId = user.id
    }

    // ÈåØÈ°åÊ¢ù‰ª∂ÔºöÂÅöÈÅéÁöÑ AND ÈÄ£Á∫åÁ≠îÂ∞ç < 3
    let query = supabase
      .from("questions")
      .select("*")
      .eq("user_id", userId)
      .gt("attempt_count", 0)
      .lt("consecutive_correct", 3)
      .is("parent_id", null)

    if (filterSubject !== "all") {
      query = query.eq("subject_id", filterSubject)
    }

    if (filterType !== "all") {
      query = query.eq("question_type_id", filterType)
    }

    // ÊéíÂ∫è
    if (sortBy === "wrong_count") {
      query = query.order("wrong_count", { ascending: false })
    } else if (sortBy === "recent") {
      query = query.order("last_attempted_at", { ascending: false })
    } else if (sortBy === "oldest") {
      query = query.order("last_attempted_at", { ascending: true })
    }

    const { data } = await query

    if (data) setQuestions(data)
  }

  useEffect(() => {
    fetchData()
  }, [])

  useEffect(() => {
    fetchQuestions()
  }, [filterSubject, filterType, sortBy])

  // ÈñãÂßãÁ∑¥ÁøíÔºàÈö®Ê©üÊàñÈ†ÜÂ∫èÔºâ
  const startPractice = (random: boolean = false) => {
    if (questions.length === 0) return

    let targetQuestion: Question
    if (random) {
      const randomIndex = Math.floor(Math.random() * questions.length)
      targetQuestion = questions[randomIndex]
    } else {
      targetQuestion = questions[0]
    }

    router.push(`/dashboard/practice/${targetQuestion.id}`)
  }

  // ÂèñÂæóÈ°åÂûãË≥áË®ä
  const getQuestionType = (typeId: string) => {
    return questionTypes.find((t) => t.id === typeId)
  }

  // ÂèñÂæóÁßëÁõÆË≥áË®ä
  const getSubject = (subjectId: string) => {
    return subjects.find((s) => s.id === subjectId)
  }

  // Ë®àÁÆóÊ≠£Á¢∫Áéá
  const getAccuracy = (question: Question) => {
    if (question.attempt_count === 0) return 0
    const correctCount = question.attempt_count - question.wrong_count
    return Math.round((correctCount / question.attempt_count) * 100)
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* È†ÅÈù¢Ê®ôÈ°å */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-800">ÈåØÈ°åÊú¨</h1>
          <p className="text-gray-600 mt-1">Ë§áÁøíÂÅöÈÅé‰ΩÜÈÇÑÊ≤íÁÜüÁ∑¥ÁöÑÈ°åÁõÆ</p>
        </div>
        {questions.length > 0 && (
          <div className="flex gap-2">
            <Button variant="outline" onClick={() => startPractice(true)}>
              üé≤ Èö®Ê©üÁ∑¥Áøí
            </Button>
            <Button
              className="bg-green-600 hover:bg-green-700"
              onClick={() => startPractice(false)}
            >
              <Play className="w-4 h-4 mr-2" />
              ÈñãÂßãÁ∑¥Áøí
            </Button>
          </div>
        )}
      </div>

      {/* Áµ±Ë®àÂç°Áâá */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardContent className="p-4 flex items-center gap-4">
            <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center">
              <Target className="w-6 h-6 text-blue-600" />
            </div>
            <div>
              <p className="text-sm text-gray-500">Â∑≤Á∑¥ÁøíÈ°åÁõÆ</p>
              <p className="text-2xl font-bold">{stats.total}</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4 flex items-center gap-4">
            <div className="w-12 h-12 bg-amber-100 rounded-lg flex items-center justify-center">
              <TrendingUp className="w-6 h-6 text-amber-600" />
            </div>
            <div>
              <p className="text-sm text-gray-500">Á∑¥Áøí‰∏≠</p>
              <p className="text-2xl font-bold">{stats.inProgress}</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4 flex items-center gap-4">
            <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
              <Award className="w-6 h-6 text-green-600" />
            </div>
            <div>
              <p className="text-sm text-gray-500">Â∑≤ÁÜüÁ∑¥</p>
              <p className="text-2xl font-bold">{stats.mastered}</p>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* ÁØ©ÈÅ∏Âàó */}
      <div className="flex flex-wrap gap-4">
        <div className="w-48">
          <Select value={filterSubject} onValueChange={setFilterSubject}>
            <SelectTrigger>
              <SelectValue placeholder="ÈÅ∏ÊìáÁßëÁõÆ" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">ÊâÄÊúâÁßëÁõÆ</SelectItem>
              {subjects.map((subject) => (
                <SelectItem key={subject.id} value={subject.id}>
                  {subject.title}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div className="w-48">
          <Select value={filterType} onValueChange={setFilterType}>
            <SelectTrigger>
              <SelectValue placeholder="ÈÅ∏ÊìáÈ°åÂûã" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">ÊâÄÊúâÈ°åÂûã</SelectItem>
              {questionTypes.map((type) => (
                <SelectItem key={type.id} value={type.id}>
                  {type.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div className="w-48">
          <Select value={sortBy} onValueChange={setSortBy}>
            <SelectTrigger>
              <SelectValue placeholder="ÊéíÂ∫èÊñπÂºè" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="wrong_count">ÈåØË™§Ê¨°Êï∏ÊúÄÂ§ö</SelectItem>
              <SelectItem value="recent">ÊúÄËøë‰ΩúÁ≠î</SelectItem>
              <SelectItem value="oldest">ÊúÄ‰πÖÊú™Á∑¥Áøí</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* È°åÁõÆÂàóË°® */}
      {questions.length === 0 ? (
        <Card>
          <CardContent className="flex flex-col items-center justify-center py-12">
            <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mb-4">
              <Award className="w-8 h-8 text-green-600" />
            </div>
            <h3 className="text-lg font-semibold text-gray-800 mb-2">
              Â§™Ê£í‰∫ÜÔºÅÊ≤íÊúâÂæÖË§áÁøíÁöÑÈ°åÁõÆ
            </h3>
            <p className="text-gray-600 text-center">
              {stats.total === 0
                ? "ÈÇÑÊ≤íÊúâÈñãÂßãÁ∑¥ÁøíÔºåÂéªÈ°åÂ∫´ÈñãÂßãÂêßÔºÅ"
                : "ÊâÄÊúâÈ°åÁõÆÈÉΩÂ∑≤Á∂ìÁÜüÁ∑¥‰∫ÜÔºåÁπºÁ∫å‰øùÊåÅÔºÅ"}
            </p>
            {stats.total === 0 && (
              <Button
                className="mt-4 bg-blue-600 hover:bg-blue-700"
                onClick={() => router.push("/dashboard/practice")}
              >
                ÂâçÂæÄÈ°åÂ∫´
              </Button>
            )}
          </CardContent>
        </Card>
      ) : (
        <div className="space-y-3">
          {questions.map((question) => (
            <MistakeCard
              key={question.id}
              question={question}
              questionType={getQuestionType(question.question_type_id)}
              subject={getSubject(question.subject_id)}
              accuracy={getAccuracy(question)}
              onPractice={() => router.push(`/dashboard/practice/${question.id}`)}
            />
          ))}
        </div>
      )}
    </div>
  )
}

// ÈåØÈ°åÂç°ÁâáÂÖÉ‰ª∂
function MistakeCard({
  question,
  questionType,
  subject,
  accuracy,
  onPractice,
}: {
  question: Question
  questionType?: QuestionType
  subject?: Subject
  accuracy: number
  onPractice: () => void
}) {
  const Icon = questionType
    ? typeIcons[questionType.name] || HelpCircle
    : HelpCircle
  const colorClass = questionType
    ? typeColors[questionType.name] || "text-gray-600 bg-gray-100"
    : "text-gray-600 bg-gray-100"
  const consecutiveCorrect = (question as any).consecutive_correct || 0

  // ÈÄ≤Â∫¶Ê¢ùÈ°èËâ≤
  const getProgressColor = () => {
    if (consecutiveCorrect >= 2) return "bg-green-500"
    if (consecutiveCorrect >= 1) return "bg-amber-500"
    return "bg-red-500"
  }

  return (
    <Card className="group hover:shadow-md transition-shadow">
      <CardContent className="p-4">
        <div className="flex items-start gap-4">
          {/* È°åÂûãÂúñÁ§∫ */}
          <div
            className={`w-10 h-10 rounded-lg flex items-center justify-center shrink-0 ${colorClass}`}
          >
            <Icon className="w-5 h-5" />
          </div>

          {/* È°åÁõÆÂÖßÂÆπ */}
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2 mb-1 flex-wrap">
              <span className="text-xs px-2 py-0.5 bg-gray-100 rounded text-gray-600">
                {subject?.title || "Êú™ÂàÜÈ°û"}
              </span>
              <span className="text-xs px-2 py-0.5 bg-gray-100 rounded text-gray-600">
                {questionType?.label || "Êú™Áü•È°åÂûã"}
              </span>
              {question.marked_for_review && (
                <span className="text-xs px-2 py-0.5 bg-amber-100 rounded text-amber-600">
                  ‚≠ê Â∑≤Ê®ôË®ò
                </span>
              )}
            </div>
            <p className="text-gray-800 line-clamp-2 mb-2">{question.content}</p>

            {/* Áµ±Ë®àË≥áË®ä */}
            <div className="flex items-center gap-4 text-sm text-gray-500">
              <span>
                ‰ΩúÁ≠î {question.attempt_count} Ê¨°
              </span>
              <span>
                Á≠îÈåØ {question.wrong_count} Ê¨°
              </span>
              <span>
                Ê≠£Á¢∫Áéá {accuracy}%
              </span>
            </div>

            {/* ÁÜüÁ∑¥Â∫¶ÈÄ≤Â∫¶Ê¢ù */}
            <div className="mt-2">
              <div className="flex items-center justify-between text-xs text-gray-500 mb-1">
                <span>ÁÜüÁ∑¥Â∫¶</span>
                <span>{consecutiveCorrect} / 3</span>
              </div>
              <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
                <div
                  className={`h-full transition-all ${getProgressColor()}`}
                  style={{ width: `${(consecutiveCorrect / 3) * 100}%` }}
                />
              </div>
            </div>
          </div>

          {/* Á∑¥ÁøíÊåâÈàï */}
          <Button
            size="sm"
            className="shrink-0 bg-green-600 hover:bg-green-700"
            onClick={onPractice}
          >
            <Play className="w-4 h-4 mr-1" />
            Á∑¥Áøí
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}
